
.postMain{
    margin-left: 50px;
    }
    
    .title{
      margin-top: 40px;
      font-size: 28px;
    }
    
    .postParentDiv{
      width: 100%;
      display: flex;
    
      flex-wrap: wrap;
      padding-bottom: 30px;
    }
    
    
    
    
    .card{
      margin-left: 18px;
      width: 300px;
      height: 275px;
      border: 1px solid #000;
      padding: 10px;
      margin-top: 18px;
    }
    
    .imageParent{
      display: flex;
      justify-content: center;
    }
    .card .imageParent .postImage{
      width: 260px;
      height: 160px;
    }
    
    .card h1{
      font-size: 22px;
      font-weight: bolder;
      margin-left: 10px;
      margin-top: 5px;
    }
    .shortDetail{
      margin-top: 5px;
      margin-left: 10px;
    }
    
    .name{
      color: rgb(1 47 52 / 64%);
        font-size: 14px;
        margin-top: 5px;
        margin-left: 10px;
    }
    
    .bottomDiv{
      display: flex;
      justify-content: space-between;
      margin-left: 10px;
      margin-top: 5px;
    }